import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Extra from './Extra';


class Problemas extends Component {

  constructor(props) {
    super(props);
    this.state = {
      problem: [],
      nickname: "",
      rol: ''

    };
  }

  componentDidMount() {

    axios.get('/problem/all')
      .then(res => {
        this.setState({ problem: res.data });
        console.log(this.state.problem);
      });

      axios.get('/User/all/role/'+this.props.cosas.email)
      .then(res => {
        this.setState({ rol: res.data });
        console.log(this.state.rol);
      });
  }


  render() {
    //const auth =new Auth();
    //const { isAuthenticated } = this.props.auth;
    //let nickname=auth.getProfile.given_name;

    /*
    if (this.state.rol==-1){
    }
    */

    return (
      <body class="custombody container-fluid">
        <div class="flex-center position-ref "> 
          <div class="container m-b-md" id="container2">
            {
              <Extra />
            }
            <ul class="breadcrumb">
              <li>Productos /<Link to="/"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Lista Productos</Link></li>
            </ul>
            <div className="container">
              <div class="panel-body">
              <table class="table table-stripe container-fluid">
                    <thead>
                      <tr>
                        <th><a>Id</a></th>
                        <th><a>Titulo</a></th>
                        <th><a>Accion</a></th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.problem.map(p =>
                          <tr>
                            <td>{p.id}</td>
                            <td>{p.title}</td>
                            <td> <Link to={`/VProblema/${p.id}`}><button type="submit" class="btn btn-default">Ver</button></Link></td>
                          </tr>
                      )}
                    </tbody>
                  </table>
                      
                  <div class="form-group">
                 <label for="nickname">Nickname:</label>
                  <input type="text" class="form-control" name="nickname" value={this.props.username} placeholder="Cualquiera" />
                </div>

                <div class="form-group">
               <button onClick={this.props.auth.login}> Login</button>
                 
                </div>

                 <div class="form-group">
               <button onClick={this.props.auth.logout}> Logout</button>
                 
                </div>



                <div class="form-group">
               <button> {this.props.cosas.name}</button>
                </div>

                 <div class="form-group">
               <button> {this.props.cosas.email}</button>
                </div>

                 <div class="form-group">
               <button> {this.props.cosas.nickname}</button>
                </div>
                
                <div class="form-group">
               <button> {this.state.rol}</button>
                </div>
                  

              </div>
            </div>
          </div>
        </div>
      </body>
    );
  }
}

export default Problemas;


