import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Extra from './Extra'
import CasoDePrueba from './CasoDePrueba';
import NotFound from './NotFound';
import UNoRegistrado from './UNoRegistrado';
import Alumno from './Alumno';
import Profesor from './Profesor';

class Home extends Component {


  constructor(props) {
    super(props);
    this.state = {
      problem: {},
      testCase: [],
      rol: ''
    };
  }

  componentDidMount() {
    axios.get('/problem/'+this.props.match.params.id)
      .then(res => {
        this.setState({ problem: res.data });
        console.log(this.state.problem);
      });

      
      axios.get('/TestCases/FromProblem/'+this.props.match.params.id)
      .then(res => {
        this.setState({ testCase: res.data });
        console.log(this.state.testCase);
      }); 
      

      axios.get('/User/all/role/'+this.props.cosas.email)
      .then(res => {
        this.setState({ rol: res.data });
        console.log(this.state.rol);
      });
  }





  render() {
    //const { title,description,id_user} = this.state.problem;
    //sthis.state.rol='1';
    if (this.state.rol=='-1'){
      //return( <UNoRegistrado />);
      this.props.history.push("/UNoRegistrado")
      return null
    }
    else if (this.state.rol=='1'){
        this.props.history.push("/Alumno")
        return null
      }
    else if (this.state.rol=='2'){
        this.props.history.push("/Profesor")
        return null
      }
      else if(this.state.rol==''){
        this.props.history.push("/Nada")
        return null
      }
      else{
        this.props.history.push("/NotFound")
        return null
      }
  
    
  }
}

export default Home


